import React, { useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { createStructuredSelector } from "reselect";
import "./side-bar.styles.css";
import { fetchAllArtistStart } from "../../redux/profile/profile.actions";
import { fetchAllSongStart } from "../../redux/songs/songs.actions";
import { fetchAllPhotoStart } from "../../redux/photo/photo.actions";
import { SelectAllSongAlbumPhotos } from "../../redux/songs/songs.selectors";
import { ProfileAllArtists } from "../../redux/profile/profile.selectors";
import FormInput from "../form-input/form-input.component";
import { ReactComponent as StarSvg } from '../asset/star.svg';
import { ReactComponent as StarSvg2 } from '../asset/star2.svg';
import { ReactComponent as User } from '../asset/user.svg';
import FormGroup from "../form-group/form-group.components";


const SideBar = ({profileAllArtists, selectAllSongAlbumPhotos, selectAllSong, handleMousever}) => {
    const HideSideBar = () => {
        document.querySelector('#side-bar').classList.toggle('no-scroll-bar')
        document.querySelector('#side-bar > .content > form').classList.toggle('reduce-form')
        document.querySelector('#side-bar > .content > form > .search-icon').classList.toggle('reposition-icon')
        document.querySelector('#side-bar').classList.toggle('reduce-width')
        document.querySelector('#side-bar > .content > .box > .right-side').classList.toggle('hide-content')
        document.querySelector('.homepage > .homepage-body').classList.toggle('increase-body')
    }
    return (
        <div id="side-bar">
            <div className="content">
                <div onClick={e => HideSideBar(e)} className="menubar"><span className="html-icon">&#9776;</span></div>
                <FormGroup SideBarType/>
                <div className="box">
                    <div className="left-side">
                        <div className="block-1 blocks">
                            <div onMouseOver={e =>  handleMousever(e, selectAllSongAlbumPhotos)} className="icon icon-1">
                                <span className="html-icon">&#10064;</span></div>
                            <div onMouseOver={e => handleMousever(e, profileAllArtists)} className="icon icon-2">
                                <span className="html-icon">&#9993;</span></div>
                                {/* <User /><span className="icon-fill icon-2-fill"></span></div> */}
                            <div onMouseOver={e => handleMousever(e, selectAllSong)} className="icon icon-3">
                                <span className="html-icon">&#9835;</span></div>
                            <div onMouseOver={e => handleMousever(e)} className="icon icon-4">
                                <span className="html-icon">&#10030;</span></div>
                        </div>
                        <div className="block-2 blocks">
                            <div onMouseOver={e => handleMousever(e)} className="icon icon-5">
                                <span className="html-icon">&#10011;</span></div>
                        </div>
                    </div>
                    <div className="right-side">
                        <div className="block-1 blocks">
                            <span onMouseOver={e => handleMousever(e, selectAllSongAlbumPhotos)} className="list list-1">Collection &#10093;</span>
                            <div className="slide-box">
                                <div className="list list-1">
                                    <span className="sub-icon"><StarSvg2/></span><span>Song&nbsp;Collection</span>
                                    <span className="list-fill list-1-fill"/>
                                </div>
                                <div className="list list-2">
                                    <span className="sub-icon"><StarSvg2/></span><span>Photo&nbsp;Album</span>
                                    <span onMouseOver={e => handleMousever(e)} 
                                            className="list-fill list-2-fill"/>
                                </div>
                            </div>
                            <span onMouseOver={e => handleMousever(e, profileAllArtists)} className="list list-2">Artist</span>
                            <span onMouseOver={e => handleMousever(e, selectAllSong)} className="list list-3">Songs</span>
                            <span onMouseOver={e => handleMousever(e)} className="list list-4">Now&nbsp;Playing</span>
                        </div>
                        <div className="block-2 blocks">
                            <span onMouseOver={e => handleMousever(e)} className="list list-5">New Playlist</span>
                            {/* <span onMouseOver={e => CreatePlayList(e)} className="list list-6">New Playlist</span> */}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = createStructuredSelector({
    selectAllSongAlbumPhotos: SelectAllSongAlbumPhotos,
    profileAllArtists: ProfileAllArtists,
    selectAllSong: SelectAllSongAlbumPhotos,
})
 const mapDispatchToProps = (dispatch) => ({
    fetchAllArtists: () => dispatch(fetchAllArtistStart()),
    fetchAllSongs: () => dispatch(fetchAllSongStart()),
    fetchAllPhotos: () => dispatch(fetchAllPhotoStart()),
 })
//  export default SideBar;
 export default connect(mapStateToProps, mapDispatchToProps)(SideBar);


