import React from 'react';
import "./items.styles.css"
import CheckboxInput from '../checkbox-input/checkbox-input.component';
import { ReactComponent as Search2 } from '../../components/asset/search-2.svg';
import { SelectProfileOfSingleArtists } from "../../redux/profile/profile.selectors";
import { createStructuredSelector } from 'reselect';
import { connect, useSelector } from "react-redux";





const Items = ({obj, handleArtistClick, handleImageCheckBox, 
    position, InnerArrayType, currentPage, selectProfileOfSingleArtists}) => {
    // console.log(obj)
    // document.querySelector('.items#new').re
    return (
        <div 
            data-index={obj.id} 
            id={obj.id}
            onClick={e => currentPage==='ARTIST-PAGE' ? handleArtistClick(e) : null} 
            className={`items ${ obj.selected ? 'is-checked' : ''} ${ currentPage==='ARTIST-PAGE' ? 'pointer' : ''}`}>
            <span className='img-holder'> 
                <img className='img-top' src={require(`../../Media/song-pic/${obj.url}.PNG`)} />
                <CheckboxInput 
                    checked={obj.selected}
                    data={position}
                    inputType='CHECKBOX1'
                    handleChange={(e) => handleImageCheckBox(e, position)}
                    name={`imageSelected${position}`}/>
            </span>
            
            {currentPage==='FIRST-PAGE' 
            ? (
                <div className='text-wrap'>
                    <div className='text'><span>Title:</span>
                        <span>{' ' + obj.title.length > 15 ?  obj.title.slice(0,13) + '...' :  obj.title}</span>
                    </div>
                    <div className='text'><span>Year:</span><span>{' ' + obj.release_year}</span></div>
                    <div className='text'><span>Label:</span><span>{' ' + obj.label}</span></div>
                    <div className='text'><span>Genre:</span><span>{' ' + obj.genre}</span></div>
                    <div className='text'><span>Artist:</span><span>{' ' + obj.artistName}</span></div>
                </div>)
                : currentPage==='ARTIST-PAGE'
                
                ? (<div className='text-wrap'>
                    <div className='text'><span>Name:</span>
                        <span>{' ' + obj.name.length > 15 ?  obj.name.slice(0,13) + '...' :  obj.name}</span>
                    </div>
                    <div className='text'><span>Songs:</span><span>{' ' + obj.songs.length + ' songs'}</span></div>
                </div>
                )
                : null
            }
            {/* <div className='items-filler'>
                <span className='btn'>
                <span className='btn-1'>VIEW PROFILE</span><span className='btn-2'>&#10064;</span>
                </span>
            </div> */}
        </div>
)};


export default Items;


