import React from 'react';
import "./views.styles.css"
import { ReactComponent as StarSvg2 } from '../../../components/asset/star2.svg';
import { SelectAllSongAlbumPhotos, SelectSortedSongs } from "../../../redux/songs/songs.selectors";
import FormSelect from '../../form-select/form-select.component';
import Items from '../../items/items.component';
import SongList from '../../song-list/song-list.component';
// import ArtistView from './artist-view.components';






const Views = ({handleArtistClick, albumPhotoCheckedState, HandleImageCheckBox, handleSelectChange,
     sortSearch, currentPage, HandleSongclick}) => { 
    const DisplayCheckButton = (e) => {
        const id= e.target.closest('div').id
        document.querySelector('.icon-place > #select').classList.toggle('dont-display')
        document.querySelector('.icon-place > #cancel').classList.toggle('dont-display')
        if (id === 'select'){
            Array.from(document.querySelectorAll('.checkbox-1')).map(itm => itm.classList.add('display-checked'))
            Array.from(document.querySelectorAll('.checkbox-2')).map(itm => itm.classList.add('display-checked'))
        } 
        else if (id === 'cancel'){
            Array.from(document.querySelectorAll('template.album-photos > .body > .items'))
                .map(itm => itm.classList.remove('is-checked'))
            Array.from(document.querySelectorAll('.checkbox-1')).map(itm => {
                itm.classList.remove('display-checked')
                return itm.firstElementChild.checked ? itm.firstElementChild.checked = false : null
            })
            Array.from(document.querySelectorAll('.checkbox-2')).map(itm => {
                itm.classList.remove('display-checked')
                return itm.firstElementChild.checked ? itm.firstElementChild.checked = false : null
            })
        }
    }
    // console.log(albumPhotoCheckedState)
    return (
            <template className='album-photos'>
                <div className='head'>
                    <h1>Albums</h1>
                    <div className='place-1'>
                        <div className='box'>
                            <span>Sort by : </span>
                            <FormSelect
                                selecttype='MidInputType2'
                                type="text"
                                handleSelectChange={handleSelectChange}
                                data={['Genre', 'Artist', 'Release Year', 'Label']}//, 'A to Z']}
                                name="sortSearch"
                                data_place="product type"/>
                        </div>
                        <div onClick={e => DisplayCheckButton(e)} className='icon-place'>
                            <div id='select'><span className="html-icon">&#10003;</span><span>Select</span></div>
                            <div id='cancel' className="dont-display"><span className="html-icon">&#10005;</span><span>Cancel</span></div>
                        </div>
                    </div>
                </div>
                <div className={`${currentPage==='FIRST-PAGE' ? 'body' : 'body-2'}`}>
                    {/* <div className='in-body'> */}
                        {/* {
                            currentPage==='FIRST-PAGE'
                            ?(
                            <div className='in-body'>{
                                albumPhotoCheckedState.map((obj, idx) => 
                                (<Items  
                                        currentPage={currentPage}
                                        position={idx}
                                        key={idx} obj={obj} 
                                        handleImageCheckBox={HandleImageCheckBox}/>
                                ))
                            }</div>
                            )
                            :  */}
                        {
                            currentPage==='FIRST-PAGE'
                            ? albumPhotoCheckedState.map((obj, idx) => 
                            (
                                <Items  
                                    currentPage={currentPage}
                                    position={idx}
                                    key={idx} obj={obj} 
                                    handleImageCheckBox={HandleImageCheckBox}/>
                            ))
                            : 
                            currentPage==='SONGS-PAGE'
                            ? albumPhotoCheckedState.map((obj, idx) => 
                            (
                                <SongList  
                                    currentPage={currentPage}
                                    HandleSongclick={HandleSongclick}
                                    // HandleCurrentSongPlay={HandleCurrentSongPlay}
                                    albumPhotoCheckedState={albumPhotoCheckedState}
                                    index={idx}
                                    key={idx} obj={obj} 
                                    handleImageCheckBox={HandleImageCheckBox}/>
                            ))
                            : 
                            currentPage==='SORT-PAGE' || currentPage==='ARTIST-PAGE' 
                            ? albumPhotoCheckedState.map((sort_array, indx) => {
                                // console.log(' i ran as as  code')
                                var cnt = 0
                                return (
                                <div key={indx} className='sorted-item-wrap'>
                                    <span className='sort-head'>{
                                        currentPage==='ARTIST-PAGE'
                                        ? sort_array[0].name.slice(0,1)
                                        : sortSearch === 'Genre'
                                        ? sort_array[0].genre
                                        : sortSearch === 'Artist'
                                        ? sort_array[0].artistName
                                        : sortSearch === 'Release Year'
                                        ? sort_array[0].release_year
                                        : sortSearch === 'Label'
                                        ? sort_array[0].label + ' Record'
                                        : null
                                    }</span>
                                    <div className={`${
                                        sort_array.length === 5
                                                ? 'sorted' 
                                                : sort_array.length < 5 || sort_array.length > 5
                                                ? 'sorted-2'
                                                : null}`}>
                                        {
                                            sort_array.map((obj, idx) => {
                                            cnt ++
                                            return (
                                                <Items
                                                    handleArtistClick={handleArtistClick} 
                                                    currentPage={currentPage}
                                                    position={cnt} key={`${indx}-${idx}`} 
                                                    obj={obj} 
                                                    handleImageCheckBox={HandleImageCheckBox}
                                                    />
                                            )})
                                        }
                                    </div>
                                </div>
                            )})
                            : null
                        }
                    {/* </div> */}
                </div>

            </template>
    )
 };

