import React, {useEffect, useMemo, useReducer, useDispatch} from 'react';
import { useState } from 'react';
import "./landingpage.styles.css"
import { createStructuredSelector } from 'reselect';
import { connect, useSelector } from "react-redux";
import { SelectAllSongAlbumPhotos, SelectSortedSongs } from "../../redux/songs/songs.selectors";
import { SelectProfileOfSingleArtists } from "../../redux/profile/profile.selectors";
import { ReactComponent as StarSvg } from '../../components/asset/star.svg';

import { ReactComponent as Replay } from '../../components/asset/replay.svg';
import { ReactComponent as CaretNext } from '../../components/asset/caret-next.svg';
import { ReactComponent as CaretRight } from '../../components/asset/caret-double-right-duotone.svg';
import { ReactComponent as CaretLeft } from '../../components/asset/caret-double-left-duotone.svg';
import { ReactComponent as Shuffle } from '../../components/asset/shuffle.svg';

import CustomButton from '../../components/custom-button/custom-button.component';
import SideBar from '../../components/side-bar/side-bar.components';
import { fetchAllSongAlbumPhotos } from "../../redux/songs/songs.selectors";
import Views from '../../components/views/views/views.components';
import ArtistView from '../../components/views/artist-view/artist-view.components';
import NowPlaying from '../../components/now-playing/now-playing.component';
import FormInput from '../../components/form-input/form-input.component';





