// const setSortSearch = i => ({type: 'SET_SORT_SEARCH', payload: i})

 const Homepage = ({fetchAllSongAlbumPhotos, selectAllSongAlbumPhotos, selectSortedSongs,
    selectProfileOfSingleArtists}) => {
    // const [state, dispatch] = useReducer(reducer, INITIAL_STATE)
    // const {albumPhotoCheckedState, freshSortingSearch, sortSearch} = state;
    const [sideBarCurrentSelector, setSideBarCurrentSelector ] = useState(selectAllSongAlbumPhotos)
    const [currentlyPlayingSong, setCurrentlyPlayingSong ] = useState({id:"",  artistName:"", title:"", format:"", label:'', 
            url:"", minutes:'', release_year:""})
    const {id, artistName, title, format, label, url, minutes, release_year} = currentlyPlayingSong
    
    const [singleArtist, setSingleArtist] = useState(null);
    const [currentPage, setCurrentPage] = useState('FIRST-PAGE');
    const [albumPhotoCheckedState, setAlbumPhotoCheckedState] = useState(selectAllSongAlbumPhotos);
    const [sortSearch, setSortSearch] = useState('')
    var SelectedSortedSearch = useSelector(SelectSortedSongs(sortSearch))
    var SelectedSingleItem = useSelector(SelectProfileOfSingleArtists(singleArtist))
    
    const HandleMousever = (e, selector) => {
        const {className, classList} = e.target
        const Card=document.querySelector('.new-playlist-card')
        const LeftSideIcon = document.querySelector('.left-side > .blocks > .icon')
        const RightSideSlideBox = document.querySelector('.right-side > .blocks > .slide-box')
        const RightSideFirstList= document.querySelector('.right-side > .blocks > .list:first-child')
        const NowPlaying = document.querySelector('#now-playing')
        const Views = document.querySelector('template.album-photos')
        const Container = document.querySelector('#container')
        const ArtistView = document.querySelector('#artist-view') 

        Card.classList.remove('show-new-playlist-card')
        ArtistView.classList.remove('display-artist-view')
        RemoveHoverColor()
        try{
            var num = className.split(' ')[1].split('-')[1]
            const SlideBoxListItem = document.querySelector(`.slide-box > .list-${num}`)
            const RightSideListItem = document.querySelector(`.right-side > .blocks > .list-${num}`)
            const LeftSideIconItem = document.querySelector(`.left-side > .blocks > .icon-${num}`)
            if (classList.contains('list-fill')){
                SlideBoxListItem.setAttribute('style', 'border-radius: 20px 0 0 20px;background-color: var(--normalblack);')
            }
            else{
                LeftSideIconItem.setAttribute('style', 'border-radius: 20px 0 0 20px; background-color: var(--normalblack);')
                RightSideListItem.setAttribute('style', 'background-color: var(--normalblack);')
            }
        }
        catch (error){}
        if (!classList.contains('list-1') && !classList.contains('icon-1')){
           Container.classList.add('container-change-color')
           if ((document.querySelector('#side-bar > .content > .box >\
               .right-side > .blocks > .slide-box.slide-open'))  && (!classList.contains('list-fill'))){
                   LeftSideIcon.classList.toggle('add-px-below')
                   RightSideFirstList.classList.toggle('add-px-below')
                   RightSideSlideBox.classList.toggle('slide-open')
               }
        } 
        else {
           LeftSideIcon.classList.toggle('add-px-below')
           RightSideFirstList.classList.toggle('add-px-below')
           e.target.innerHtml = '<span>Collection &#10097</span>'
           RightSideSlideBox.classList.toggle('slide-open')
           Container.classList.remove('container-change-color')
        }
         if (selector){
            if (classList.contains('list-1') || classList.contains('icon-1')){
                setCurrentPage('FIRST-PAGE')
            }
            if (classList.contains('list-2') || classList.contains('icon-2')){ 
                setCurrentPage('ARTIST-PAGE')
            }
            if (classList.contains('list-3') || classList.contains('icon-3')){
                setCurrentPage('SONGS-PAGE')
            }
            setAlbumPhotoCheckedState(selector)
            NowPlaying.classList.remove('display-now-playing')
            Views.classList.remove('no-display-album-photos')
         } 
         else if (classList.contains('list-4') || classList.contains('icon-4')){
                Views.classList.add('no-display-album-photos')
                NowPlaying.classList.add('display-now-playing')
         }
         else if (classList.contains('list-5') || classList.contains('icon-5')){
                Card.classList.toggle('show-new-playlist-card')
                Card.styles.top = `${e.target.offsetTop}px`
         }
    }

    const handleSelectChange = (e) => {
        const {name, value} = e.target;
        RemoveHoverColor()
        setAlbumPhotoCheckedState(SelectedSortedSearch)
        setCurrentPage('SORT-PAGE')
        setSortSearch(value)
    } 

    const handleArtistClick = (e) => {
        const id = e.target.closest('.items').id
        setSingleArtist(id)
        setCurrentPage('ARTIST-VIEW-PAGE')
        document.querySelector('template.album-photos').classList.add('no-display-album-photos')
        document.querySelector('#artist-view').classList.add('display-artist-view')
    }

    const HandleSongclick = (e, obj) => {
        setCurrentlyPlayingSong({id:obj.id, artistName:obj.artistName, format:obj.format, title:obj.title, label:obj.label, 
            url:obj.url, minutes:obj.minutes, release_year:obj.release_year})
        const songLi = e.target.closest('.song-li-wrap')
        // const {id} = songLi.dataset

        //TO ADJUST THE HEIGHT AND MAKE WAY FOR BOTTOM PALLETE TO SLIDE UP
        Array.from(document.querySelectorAll('.song-li > .song-li-wrap')).map(itm =>  itm !== songLi
            ? itm.classList.remove('song-li-change-bg') : null)
        songLi.classList.toggle('song-li-change-bg')
        const mainPlace_2 = songLi.firstElementChild.firstElementChild.nextElementSibling
        const otherPlace_2 = Array.from(document.querySelectorAll('.song-li-wrap > .item-1 > .place-2')).map(itm =>  
            itm !== mainPlace_2 ? itm.classList.remove('display-place-2') : null)
        const otherFirstDIV = Array.from(document.querySelectorAll('.song-li-wrap .item-1 > .place-1 > .hide-div1'))
        const otherSecondDIV = Array.from(document.querySelectorAll('.song-li-wrap .item-1 > .place-1 > .hide-div2'))
        const mainFirstDIV = songLi.firstElementChild.firstElementChild.firstElementChild.nextElementSibling
        const mainSecondDIV = mainFirstDIV.nextElementSibling
        // console.log(otherSecondDIV)
        // console.log(otherFirstDIV)
        if (otherSecondDIV.length !== 0){
            // otherFirstDIV.map(itm => itm.classList.toggle('hide-div1'))
            otherSecondDIV.map(itm => itm.setAttribute('display', 'block'))
        }
        mainFirstDIV.classList.toggle('hide-div1')
        mainSecondDIV.classList.toggle('hide-div2')
        mainPlace_2.classList.toggle('display-place-2')
        songLi.querySelector('.item-1').classList.toggle('re-spacing')
    } 

    const HandleImageCheckBox = (e, position) => {
      var cnt = 0
      const updatedCheckedState = albumPhotoCheckedState.map((obj, index) => {
        if (index === position && currentPage!=='SORT-PAGE'){
            return {...obj, selected: !obj.selected} 
        }
        else if (currentPage==='SORT-PAGE'){
            return obj.map(itm => {
                cnt ++
                return cnt === position ? {...itm, selected: !itm.selected} : {...itm}
            })
        }
        return {...obj}
      });
      setAlbumPhotoCheckedState(updatedCheckedState); 
    }

    const oldPlayingSongs = useMemo(() => ({...currentlyPlayingSong}), [])

    useEffect(() => {
        
        if (SelectedSortedSearch.length !== 0 && currentPage==="SORT-PAGE"){
            setAlbumPhotoCheckedState(SelectedSortedSearch)
        } 
        // THIS BLOCK IS FOR DISPLAYING THE BOTOM PALLETE WHENEVER USER CLICKS ON A SONG
        if (document.querySelector('.song-li-change-bg')){
            document.querySelector('#side-bar').classList.add('reduce-height-for-btm-pallete')
            document.querySelector('.homepage > .homepage-body').classList.add('reduce-height-for-btm-pallete')
            document.querySelector('.homepage > .bottom-pallete').classList.add('display-bottom-pallete')
        } else {
            setCurrentlyPlayingSong(oldPlayingSongs)
            document.querySelector('#side-bar').classList.remove('reduce-height-for-btm-pallete')
            document.querySelector('.homepage > .homepage-body').classList.remove('reduce-height-for-btm-pallete')
            document.querySelector('.homepage > .bottom-pallete').classList.remove('display-bottom-pallete')
        }
    },[sortSearch, singleArtist, currentPage, currentlyPlayingSong])

    const RemoveHoverColor = () => {
        const AllIcons= Array.from(document.querySelectorAll('.left-side > .blocks > .icon'))
        const AllLists= Array.from(document.querySelectorAll('.right-side > .blocks > .list'))
        const AllSlideBoxLists= Array.from(document.querySelectorAll('.right-side > .blocks > .slide-box > .list'))
        AllIcons.map(itm => itm.removeAttribute('style', 'border-radius:20px 0 0 20px; background-color: var(--normalblack);'))
        AllLists.map(itm => itm.removeAttribute('style', 'background-color: var(--normalblack);'))
        AllSlideBoxLists.map(itm => itm.removeAttribute('style', 'background-color: var(--normalblack);'))
    }

    const CancelCard = () => {
        window.onclick = (e) => {
            if (e.target.classList.contains('new-playlist-card')){
                RemoveHoverColor()
                document.querySelector('.homepage > .homepage-body > .new-playlist-card')
                        .classList.remove('show-new-playlist-card')
            }
        }
    }
    // },[sortSearch, singleArtist, currentlyPlayingSong])
    return (
        <div className='homepage'>
            <SideBar handleMousever={HandleMousever} />
            <div className='homepage-body'>
                <div className='content'>
                    <Views 
                        albumPhotoCheckedState={albumPhotoCheckedState}
                        currentPage ={currentPage}
                        sortSearch={sortSearch}
                        singleArtist={singleArtist}
                        handleArtistClick={handleArtistClick}
                        // HandleCurrentSongPlay={HandleCurrentSongPlay}
                        HandleSongclick={HandleSongclick}
                        HandleImageCheckBox={HandleImageCheckBox}
                        handleSelectChange={(e) => handleSelectChange(e)} />
                    <ArtistView 
                        data={SelectedSingleItem}
                        currentPage ={currentPage}/>
                    <NowPlaying  
                        HandleSongclick={HandleSongclick}
                        handleImageCheckBox={HandleImageCheckBox}
                        obj={currentlyPlayingSong}/>
                </div>
                <div className="new-playlist-card" onClick={e => CancelCard()}>
                    <div className='card-wrap' >
                        <span>Name This Playlist</span>
                        <FormInput inputtype='SearchInputType'/>
                        <span><CustomButton buttonType="FlatButton" >Save</CustomButton></span>
                    </div>
                </div>
            </div>
            <div className='bottom-pallete'>
                <div className='wrap'>
                    <div className='first-line'>
                        <div className='box-2'>
                            <span className='time'>0:44</span>
                            <span className='line'><span className='circle'></span></span>
                            <span className='time'>{minutes}</span>
                        </div>
                    </div>
                    <div className='second-line'>
                        <div className='box-1'>
                            <span className='img-holder'> 
                            { url
                                ? <img className='img-top' src={require(`../../Media/song-pic/${url}.PNG`)} />
                                : null
                            }
                            </span>
                            <div>
                                <span className='song'>{`${artistName + ' || ' + label + '('+ format +')'}`}</span>
                                <span className='name'>{title}</span>
                            </div>
                        </div>
                        <div className='box-2'>
                            <span className='time'>0:44</span>
                            <span className='line'><span className='circle'></span></span>
                            <span className='time'>{minutes}</span>
                        </div>
                        <div className='box-3'>
                            <span><CaretLeft /></span>
                            <span><CaretNext /></span>
                            <span><CaretRight /></span>
                            <span><Shuffle /></span>
                            <span><Replay /></span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
 };

const mapStateToProps = createStructuredSelector({
    selectAllSongAlbumPhotos: SelectAllSongAlbumPhotos,
    selectSortedSongs: SelectSortedSongs,
    selectProfileOfSingleArtists: SelectProfileOfSingleArtists
})
//  const mapDispatchToProps = (dispatch) => ({
//     clickedSlideImagesStart: (url) => dispatch(fetchClickedSlideImagesStart(url))
//  })
//  export default Homepage;
 export default connect(mapStateToProps)(Homepage);
