    //     if (id === 'google-play'){
    //         document.querySelector('#header > .content > .box-2 > .menubar').setAttribute('style', 'display:none')
    //         document.querySelector('#header > .content > .box-2 > .menutimes').setAttribute('style', 'display: block;')
    //         if (document.querySelector('template#menubar-hover')){
    //             document.querySelector('template#menubar-hover').classList.remove('slide-up-menu-bar')
    //             document.querySelector('template#menubar-hover').classList.add('slide-down-menu-bar')
    //             document.querySelector('template#menubar-hover.slide-down-menu-bar').setAttribute('style', 'animation: slideDownMenuBar 500ms ease-in 1;')
    //         }//display:block')
    //     }
    //     if (id === 'crown'){
    //         document.querySelector('#header > .content > .box-2 > .menutimes').setAttribute('style', 'display:none')
    //         document.querySelector('#header > .content > .box-2 > .menubar').setAttribute('style', 'display:block')
